<template>
  <transition name="slide-y-transition">
    <v-dialog ref="dateDialog" v-model="dateModal" v-if="needDate" :return-value.sync="selectedDate" persistent lazy full-width width="290px">
      <v-text-field slot="activator" :rules="[rules.required]" v-model="selectedDate" color="green darken-3" label="Дата завершення завдання" prepend-icon="event" readonly></v-text-field>
      <v-date-picker locale="uk" :min="dateMin" v-model="selectedDate" scrollable color="green darken-3">
        <v-spacer></v-spacer>
        <v-btn flat color="grey--text text--darken-2" @click="cancel">Скасувати</v-btn>
        <v-btn color="green darken-3 white--text" @click="onSelectedDate">OK</v-btn>
      </v-date-picker>
    </v-dialog>
  </transition>
</template>

<script>
export default {
  props: {
    onDate: {},
    needDate: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      selectedDate: null,
      dateModal: false,
      rules: {
        required: value => !!value || "Обов'язкове поле."
      }
    };
  },
  watch: {
    needDate(val) {
      if (val == false) {
        this.selectedDate = null;
        this.$refs.dateDialog.save(this.selectedDate);
        this.onDate(this.selectedDate);
      }
    }
  },
  computed: {
    dateMin() {
      let now = new Date();
      let date = now.getDate();
      let month = now.getMonth();
      const year = now.getFullYear();
      date = date < 10 ? `0${date}` : date;
      month = month < 9 ? `0${month + 1}` : month + 1;
      return `${year}-${month}-${date}`;
    }
  },
  methods: {
    onSelectedDate() {
      this.$refs.dateDialog.save(this.selectedDate);
      this.onDate(this.selectedDate);
    },
    cancel() {
      this.dateModal = false;
    }
  }
};
</script>



